.nav {
    width: 100%;
    height: 50px;
    background-color: rgb(216, 216, 216);
    position: fixed;
    top: 0;
    display: flex;
    align-items: center;
    font-family: 'Prompt', sans-serif;
    overflow: hidden;
}

.topLeft,.topRight {
    flex: 3;
    display: flex;
    align-items: center;
    justify-content: center;
}

.title {
    flex: 3;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: bold;
}

.topCenter {
    flex: 6;
}

.topIcon {
    font-size: 20px;
    margin-right: 10px;
    color: #444;
    cursor: pointer;
}

.topImg {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    object-fit: cover;
}

.topList {
    display: flex;
    justify-content: center;
    margin: 0;
    padding: 0;
    list-style: none;
}

.topListItem {
    margin-right: 20px;
    font-size: 18px;
    font-weight: 300;
    cursor: pointer;
}

.Name {
    margin-top: 7px;
    cursor: default;
}

.Logout {
    margin-top: 7px;
}

.Img {
    cursor: default;
}